/* You can add global styles to this file, and also import other style files */
@import '~normalize.css/normalize.css';
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700&family=Urbanist:ital,wght@0,100;0,200;0,300;0,400;1,100;1,200;1,300&display=swap");
@import '~primeicons/primeicons.css';
@import '~primeng/resources/themes/saga-blue/theme.css';
@import '~primeng/resources/primeng.min.css';
@import '~primeflex/primeflex.css';
@import '~animate.css/animate.css';
body, .p-component {
  font-family: "Open Sans", "sans-serif";
  font-size: 14px;
}

a {
  text-decoration: none;
}

header {
  margin-bottom: 25px;
}
header .logo {
  cursor: pointer;
}
header .logo img {
  width: 160px;
}
header .navigation {
  padding-top: 22px;
}
header .tools {
  padding-top: 38px;
}
header .tools .pi {
  margin-right: 10px;
}
header .search {
  padding-top: 25px;
}

footer {
  background-color: var(--yellow-100);
  min-height: 50px;
  padding: 10px 25px;
  width: 100%;
  margin-top: 25px;
}
footer .footer-wrapper {
  align-items: center;
}
footer .footer-wrapper .social {
  margin-top: 15px;
  text-align: right;
}
footer .footer-wrapper .social .nav {
  justify-content: flex-end;
}

.nav li {
  list-style: none;
  display: inline-block;
  margin-right: 15px;
}
.nav li a {
  color: var(--surface-500);
}
.nav li a:hover {
  color: #ff3a00;
  cursor: pointer;
}

.products-search {
  display: flex;
  background-color: var(--surface-200);
  border-radius: 10px;
  width: 275px;
  height: 35px;
}
.products-search .pi {
  width: 25px;
  font-size: 1.3em;
  color: #ff3a00;
  margin-top: 10px;
  margin-left: 10px;
}
.products-search-input {
  height: 35px;
  background-color: transparent;
  color: var(--surface-600);
  border: 0;
  width: 100%;
}
.products-search-input:focus, .products-search-input:active {
  border: 0;
  outline: none;
}

.categories-banner {
  margin-top: 75px;
}
.categories-banner h3 {
  margin-bottom: 20px;
  color: var(--surface-700);
}
.categories-banner .category {
  min-height: 100px;
  border-radius: 14px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #fff;
  cursor: pointer;
}
.categories-banner .category-icon .pi {
  font-size: 2em;
  margin-bottom: 10px;
}
.categories-banner .category-icon-name {
  font-size: 1em;
}
.categories-banner .category:hover {
  transform: scale(1.1);
  transition: all 0.5s ease;
}

.product-item-wrapper {
  background-color: var(--surface-200);
  border: 1px solid var(--surface-300);
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 250px;
  margin-top: 85px;
}
.product-item .image {
  width: 60%;
  height: 150px;
  margin-top: -85px;
  cursor: pointer;
}
.product-item .name {
  margin: 10px 0;
}
.product-item .price {
  color: #ff3a00;
  font-size: 1.2em;
  margin: 0;
}
.product-item .add-to-cart {
  background-color: #ff3a00;
  border: 0;
  border-radius: 15px;
}
.product-item .add-to-cart:enabled:hover {
  background-color: #af2900;
}

.product-name {
  font-size: 2em;
}
.product-desc {
  font-size: 1.4em;
}
.product-rating {
  margin: 15px 0;
}
.product-price {
  font-size: 2em;
  font-weight: 600;
  border-bottom: 1px solid var(--surface-300);
  padding: 25px 0;
}
.product-price .price-before {
  font-size: 0.7em;
  margin-left: 10px;
  font-weight: 200;
  color: var(--surface-500);
  text-decoration: line-through;
}
.product-quantity {
  margin: 25px 0;
}
.product-quantity label {
  font-size: 1.2em;
}
.product-quantity .p-button {
  background-color: #ff3a00;
  border: 0;
}
.product-quantity .p-button:enabled:hover {
  background-color: #af2900;
  border: 0;
}
.product-actions .p-button {
  background-color: #ff3a00;
  border: 0;
}
.product-actions .p-button:enabled:hover {
  background-color: #af2900;
  border: 0;
}

.product-rich-desc {
  line-height: 1.6;
}

.banner {
  height: 280px;
  width: 100%;
  background-color: #D7EFF8;
  border-radius: 15px;
}
.banner-text {
  color: var(--surface-700);
  margin-top: 50px;
}
.banner-text h1 {
  margin: 0;
  font-size: 3em;
  font-weight: 400;
}
.banner-text h2 {
  margin: 0;
  font-size: 2em;
  font-weight: 300;
}
.banner-image {
  padding: 15px 0 0 0;
  text-align: center;
}
.banner-button {
  background-color: var(--surface-0);
  color: #ff3a00;
  border: 0;
  height: 50px;
  border-radius: 13px;
}
.banner-button:enabled:hover {
  background-color: #ff3a00;
  color: var(--surface-0);
  border: 0;
  transition: all 0.5s ease;
}

.gallery .main-image {
  text-align: center;
  margin-bottom: 15px;
  height: 600px;
}
.gallery .main-image img {
  max-width: 100%;
  height: 100%;
}
.gallery .images ul {
  display: flex;
  margin: 0;
  padding: 0;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.gallery .images ul li {
  background-size: cover;
  width: 100px;
  height: 100px;
  display: block;
  margin-right: 15px;
}

.cart-page {
  min-height: 500px;
}
.cart-page button {
  background-color: #ff3a00;
  border: 0;
}
.cart-page button:enabled:hover {
  background-color: #af2900;
}
.cart-page .cart-shipping {
  color: var(--green-700);
}
.cart-page .cart-item {
  background-color: var(--surface-0);
  border: 1px solid var(--surface-300);
  padding: 15px;
  border-radius: 15px;
}
.cart-page .cart-item-image img {
  height: 120px;
  max-width: 100%;
}
.cart-page .cart-item-name {
  font-weight: 600;
  font-size: 1.2em;
  margin-bottom: 15px;
}
.cart-page .cart-item-price {
  font-weight: 600;
  font-size: 1.2em;
  margin-bottom: 15px;
  color: #ff3a00;
}
.cart-page .cart-item-quantity {
  margin: 15px 0;
}
.cart-page .cart-item-subtotal {
  font-weight: 600;
}
.cart-page .cart-item-subtotal-value {
  color: var(--green-600);
}

.order-summary {
  background-color: var(--surface-0);
  padding: 15px;
  border: 1px solid var(--surface-200);
}
.order-summary .summary-price {
  display: flex;
  margin: 15px 0;
}
.order-summary .summary-price span {
  display: block;
  width: 80%;
}
.order-summary .summary-price span:last-child {
  width: 20%;
  font-weight: 600;
}
.order-summary .summary-price span.free {
  color: green;
}
.order-summary .checkout-button button {
  width: 100%;
}

.checkout-page {
  min-height: 500px;
}
.checkout-page button {
  background-color: #ff3a00;
  border: 0;
}
.checkout-page button:enabled:hover {
  background-color: #af2900;
}
.checkout-page .checkout-form {
  margin-top: 25px;
}
.checkout-page .checkout-button {
  margin-top: 20px;
}
.checkout-page .checkout-button button {
  width: 100%;
}

.login-register {
  background-image: linear-gradient(235deg, #565867, #191a25);
  height: 100vh;
}
.login-register-panel {
  width: 650px;
  height: 525px;
  background-color: var(--surface-a);
  position: absolute;
  left: calc(50% - 325px);
  top: calc(50% - 200px);
}
.login-register .full-btn {
  width: 100%;
}

.container {
  width: 1200px;
  padding: 15px;
  margin: 0 auto;
  background-color: var(--surface-100);
}

button {
  background-color: #ff3a00;
  border: 0;
}
button:enabled:hover {
  background-color: #af2900;
}