.categories-banner {
    margin-top: 75px;

    h3{
        margin-bottom: 20px;
        color: var(--surface-700);

    }

    .category {
        min-height: 100px;
        border-radius: 14px;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        background-color: #fff;
        cursor: pointer;

        &-icon {
            .pi{
                font-size: 2em;
                margin-bottom: 10px;
            }

            &-name {
                font-size: 1em;
            }
        }

        &:hover{
            transform: scale(1.1);
            transition: all 0.5s ease;
        }
    }
}