.product {
    &-name {
        font-size: 2em;
    }

    &-desc {
        font-size: 1.4em;
    }

    &-rating{
        margin: 15px 0;
    }
    &-price{
        font-size: 2em;
        font-weight: 600;
        border-bottom: 1px solid var(--surface-300);
        padding: 25px 0;

        .price-before {
            font-size: 0.7em;
            margin-left: 10px;
            font-weight: 200;
            color: var(--surface-500);
            text-decoration: line-through;
        }
    }

    &-quantity{
        margin: 25px 0;
        label {
            font-size: 1.2em;
        }

        .p-button {
            background-color: $primary-color;
            border: 0;

            &:enabled:hover {
                background-color: $primary-color-dark;
                border: 0;
            }
        }
    }

    &-actions {
        .p-button {
            background-color: $primary-color;
            border: 0;

            &:enabled:hover {
                background-color: $primary-color-dark;
                border: 0;
            }
        }
    }
}

.product-rich-desc{
    line-height: 1.6;
}